import React from 'react'
import { Helmet } from 'react-helmet'

const Meta = ({ title, description, keywords }) => {
  return (
    <Helmet>
      <title>{title}</title>
      <meta name='description' content={description} />
      <meta name='keyword' content={keywords} />
    </Helmet>
  )
}

Meta.defaultProps = {
  title: 'Welcome To BibiCraft',
  description: 'Fine arts and crafts',
  keywords: 'crafts, paintings, sculpures, clothes, jewelry',
}

export default Meta
